body {
  background-color: #283618;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.passGenForm {
  height: 500px;
  width: 400px;
  background-color: #dda15e;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.formHeader {
  background-color: #bc6c25;
  height: 70px;
  width: 400px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  text-align: center;
  color: #fefae0;
  text-shadow: 3px 4px 5px #0f0f0f;
}

label {
  margin: 10px;
  font-size: 18px;
  font-weight: 500;
}

input[type="checkbox"] {
  accent-color: #b16018;
}

.formMain {
  min-height: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  margin-top: 30px;
}

.formButton {
  display: flex;
  gap: 20px;
  margin-top: 30px;
}

input[type="text"] {
  color: rgb(42, 35, 35);
  font-size: 16px;
  font-weight: 600;
  line-height: 20px;
  min-width: 300px;
  min-height: 28px;
  border-radius: 4px;
  padding: 8px 16px;
  border: 2px solid transparent;
  box-shadow: rgb(0 0 0 / 12%) 0px 1px 3px, rgb(0 0 0 / 24%) 0px 1px 2px;
  background: rgb(251, 251, 251);
}

button {
  /* background-image: linear-gradient(60deg, #29323c 0%, #485563 100%); */
  background-image: linear-gradient(to top, #384b21 0%, #669531 100%);
  border: 0;
  border-radius: 12px;
  color: #ffffff;
  cursor: pointer;
  display: inline-block;
  font-family: -apple-system, system-ui, "Segoe UI", Roboto, Helvetica, Arial,
    sans-serif;
  font-size: 16px;
  font-weight: 500;
  min-width: 150px;
  line-height: 2.5;
  outline: transparent;
  padding: 0 1rem;
  text-align: center;
  text-decoration: none;
  transition: box-shadow 0.2s ease-in-out;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
}

button:hover {
  box-shadow: 0 0 0.25rem #384b21, -0.125rem -0.125rem 1rem #669531;
}

input[type="range"] {
  -webkit-appearance: none;
  margin-right: 15px;
  width: 300px;
  height: 7px;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 5px;
  background-image: linear-gradient(to top, #384b21 0%, #669531 100%);
  background-repeat: no-repeat;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: #384b21;
  cursor: ew-resize;
  box-shadow: 0 0 2px 0 #555;
}

input[type="range"]::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  box-shadow: none;
  border: none;
  background: transparent;
}
.formMain p {
  margin: 0 0 0 0;
  font-size: 25px;
  font-weight: 700;
  text-transform: uppercase;
  color: white;
  text-shadow: 3px 4px 5px #0f0f0f;
}

.passGenForm nav {
  margin-top: 10px;
  font-size: 15px;
  color: white;
  font-weight: 600;
  text-shadow: 3px 4px 5px #0f0f0f;
}
